export default {
  name: 'newsDetail',
  components: {

  },
  data () {
    return {
      info: {
        title: '',
        info: '',
        messageDate: ''
      }
    }
  },
  created () {
    this.info.title = this.$route.query.title
    this.info.info = this.$route.query.info
    this.info.messageDate = this.$route.query.messageDate
  },
  methods: {
    getBack () {
      this.$router.go(-1)
    }
  }
}



